@font-face {
  font-family: "InterWeb";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(./Inter-Regular.woff2) format("woff2"),
    url(./Inter-Regular.ttf) format("truetype"),
    url(./Inter-Regular.otf) format("opentype");
}

